@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html { font-family: 'Inter', sans-serif; }
@supports (font-variation-settings: normal) {
  html { font-family: 'Inter var', sans-serif; }
}

body {
  background: #7fbcff;
  margin: 0;
}

* {
  font-family: 'Inter', sans-serif;
}

.app {
  width: 1440px;
  margin: 8px auto;
  display: flex;
}

.app .menu.hidden {
  display: none;
}

.app .menu {
  width: 293px;
  border-radius: 20px;
  box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  margin-top: 8px;
  margin-left: 66px;
  overflow: hidden;
}

.notes-container {
  margin-left: 16px;
  display: flex;
  flex-direction: column;
}

.add-content-button {
    width: 359px;
    display: flex;
    padding: 12px 12px 16px 54px;
    border-radius: 13px;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.1);
    border: none;
    outline: none;
    cursor: pointer;
    line-height: 1.26;
    font-size: 23px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    position: relative;
}

.add-content-button .add-content-button__add-sign {
    position: absolute;
    top: 18px;
    left: 20px;
    display: flex;
    widtH: 20px;
    height: 20px;
}

.menu-item {
    display: flex;
    padding: 9px 20px;
    align-items: center;
    cursor: pointer;
    position: relative;
}

.menu-item:hover {
    background-color: rgba(0,0,0,0.1);
}

.menu-item p {
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: -0.41px;
    color: #000000;
    margin: 0;
}

.menu-item img {
    margin-right: 12px;
    width: 32px;
    height: 32px;
}

.menu-item:not(:last-child):before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    height: 1px;
    width: calc(100% - 43px);
    border-bottom: 1px solid #979797;
    opacity: 0.1;
}

.add-note-container {
    margin-bottom: 16px;
}
.add-note-container.hidden {
    display: none;
}
.add-note {
    min-height: calc(256px - 68px);
    width: calc(756px - 24px);
    background: #fff;
    border-radius: 8px;
    padding: 24px 8px 44px 16px;
    position: relative;
}
.add-note form {
    display: flex;
    flex-direction: column;
}
.add-note__title {
    font-size: 28px;
    border: none;
    outline: none;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: 0.34px;
    color: #000000;
    margin: 0 0 5px 0;
}

.add-note__title::-webkit-input-placeholder {
    opacity: 0.2;
    font-size: 28px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: 0.34px;
    color: #000;
}

.add-note__title::-moz-placeholder {
    opacity: 0.2;
    font-size: 28px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: 0.34px;
    color: #000;
}

.add-note__title:-moz-placeholder{
    opacity: 0.2;
    font-size: 28px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: 0.34px;
    color: #000;
}

.add-note__title:-ms-input-placeholder {
    opacity: 0.2;
    font-size: 28px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: 0.34px;
    color: #000;
}

.add-note__note {
    color: #000;
    font-size: 17px;
    border: none;
    outline: none;
    resize: none;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: -0.41px;
    margin: 0;
    padding: 0;
}


.add-note__note::-webkit-input-placeholder {
    opacity: 0.2;
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: -0.41px;
    color: #000000;
}

.add-note__note::-moz-placeholder {
    opacity: 0.2;
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: -0.41px;
    color: #000000;
}

.add-note__note:-moz-placeholder{
    opacity: 0.2;
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: -0.41px;
    color: #000000;
}

.add-note__note:-ms-input-placeholder {
    opacity: 0.2;
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: -0.41px;
    color: #000000;
}

.add-note__color {
    position: absolute;
    z-index: -100;
    bottom: 0;
    left: 0;
    display: none;
}

.add-note__color-wrapper {
    position: absolute;
    bottom: 8px;
    left: 8px;
    cursor: pointer;
}
.add-note__color-picker.hidden {
    display: none;
}
.add-note__color-picker {
    display: flex;
    flex-direction: column;
}
.add-note__color-picker img {
 margin-bottom: 8px;
}
.add-note__submit {
    width: 193px;
    height: 34px;
    border-radius: 7px;
    background-color: #01ce87;
    border: none;
    outline: none;
    bottom: 8px;
    right: 8px;
    position: absolute;
    font-size: 16px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.25;
    letter-spacing: 0.47px;
    text-align: center;
    color: #ffffff;
    cursor: pointer;
}

.add-note__submit:hover {
    background: #07c080;
}


.note-item {
    min-height: calc(255px - 68px);
    width: calc(756px - 24px);
    background: #fff;
    border-radius: 8px;
    padding: 25px 8px 44px 16px;
    position: relative;
    margin-bottom: 16px;
}

.note-item h2 {
    font-size: 28px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.21;
    letter-spacing: 0.34px;
    color: #000000;
    margin: 0 0 6px 0;
}

.note-item p {
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: -0.41px;
    color: #000000;
    margin: 0;
}

